import React, {Component} from 'react';
import debounce from 'lodash/debounce';
import './App.css';
import data from './app.data';

import Header from './Header/Header';
import Footer from './Footer/Footer';
import SliderSection from './SliderSection/SliderSection';
import About from './AboutSection/About';

export default class App extends Component {
  constructor() {
    super();

    this.state = {
      isMobile: App.isMobileView(),
      headerInverse: false,
      backToTopHidden: true,
    };

    this.handleWindowSizeChange = this.handleWindowSizeChange.bind(this);
    this.handleScrollChange = this.handleScrollChange.bind(this);
    this.handleBackToTop = this.handleBackToTop.bind(this);

    this.debounceScrollChange = this.debounceScrollChange.bind(this);
  }

  componentWillMount() {
    window.addEventListener('resize', this.handleWindowSizeChange);
    window.addEventListener('scroll', this.debounceScrollChange);
  }

  componentDidMount() {
    document.getElementById('preloader').classList.add('hidden');
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleWindowSizeChange);
    window.removeEventListener('scroll', this.debounceScrollChange);
  }

  handleWindowSizeChange() {
    this.setState({isMobile: App.isMobileView()});
  };

  debounceScrollChange = debounce(this.handleScrollChange, 100, {maxWait: 150});

  handleScrollChange() {
    const scrollPosition = window.pageYOffset || document.documentElement.scrollTop;
    this.setState({headerInverse: scrollPosition > 0});
  }

  handleBackToTop(newValue) {
    this.setState({backToTopHidden: newValue});
  }

  static isMobileView() {
    //Keep in sync with $breakpoint-mobile from vars
    return window.innerWidth <= 767;
  }

  render() {

    return (
        <div className="App">
          <Header headerInverse={this.state.headerInverse}
                  data={data.header}/>
          <SliderSection/>
          <About/>
          <Footer/>
        </div>
    );
  }
}



