import React, {Component} from 'react';
import './SlideShow.css';
import Slider from 'react-slick';
import translationsImg from '../../../assets/images/traduceri_resized.jpg';
import interpretareImg from '../../../assets/images/interpretare_resized.jpg';

export default class SlideShow extends Component {
  render() {

    return (
        <div className="slider">
          <Slider {...this.props.settings}>

            {/*Slide One*/}
            <div className="slider__item">
              <div className="item__image img item__image--first">
                <img src={translationsImg} alt=""/>
              </div>
              <div className="item__description">
                <div className="item__description__title">
                  <p>Serviciile noastre</p>
                  <h1>Traduceri autorizate<span className="break">Portugheza - Romana</span></h1>
                </div>
                <div className="item__description__buttons">
                  <a href="#" className="button transparent large">Contacteaza-ne</a>
                </div>
              </div>
            </div>

            {/*Slide Two*/}
            <div className="slider__item">
              <div className="item__image img item__image--second">
                <img src={interpretareImg} alt="My Company name"/>
              </div>
              <div className="item__description">
                <div className="item__description__title">
                  <p>Serviciile noastre</p>
                  <h1>Interpretariat</h1>
                </div>
                <div className="item__description__buttons">
                  <a href="#" className="button transparent large">Contacteaza-ne</a>
                </div>
              </div>
            </div>
          </Slider>
        </div>
    );
  }
}


