import React, {Component} from 'react';
import PropTypes from 'prop-types';

export default class NavItem extends Component {
  constructor(props) {
    super(props);
    this.state = {expanded: false,};

    this.toggleExpanded = this.toggleExpanded.bind(this);
  }

  toggleExpanded() {
    this.setState(prevState => ({expanded: !prevState.expanded}));
  }

  render() {
    const expanded = this.state.expanded ? ' open ' : '';
    const parentClasses = this.props.extraClasses ? this.props.extraClasses : '';

    return (
        <li onClick={this.toggleExpanded}>
          <a href="/">
            <span>{this.props.displayName}</span>
            {this.props.children ? <i className="fa fa-angle-down"/> : null}
          </a>
          {
            this.props.children &&
            <ul className={`dropMenu ${expanded} ${parentClasses}`}>
              {this.props.children.map((child, i) =>
                  <li key={i}>
                    <a href="/">{child.displayName}</a>
                  </li>,
              )}
            </ul>
          }
        </li>

    );
  }
}

NavItem.propTypes = {
  displayName: PropTypes.string,
  target: PropTypes.string,
  redirectRoute: PropTypes.string,
  children: PropTypes.arrayOf(PropTypes.shape({
    displayName: PropTypes.string,
    target: PropTypes.string,
    redirectRoute: PropTypes.string,
  }))
};



