import React, {Component} from 'react';
import './Footer.css';
import logo from '../../assets/images/logo_baltag_3.png';

export default class Footer extends Component {
  render() {
    const altCompanyLogo = 'Baltag Traduceri';
    const appLinks = [
      {
        name: 'Acasa',
        href: '#',
      }, {
        name: 'Traduceri',
        href: '#',
      }, {
        name: 'Interpretare',
        href: '#',
      }, {
        name: 'Despre noi',
        href: '#',
      },
    ];

    return (
        <footer className="mastFooter">
          <div className="inner">

            <div className="container">
              <div className="row">

                <div className="col-sm-3">
                  <div className="logo"><a href="#">
                    <img src={logo} alt={altCompanyLogo}/></a>
                  </div>
                  <p className="copyright">&copy; Site 2018. All rights reserved.</p>
                </div>

                <div className="col-sm-3">
                  <h6>Links</h6>
                  <ul className="links">
                    {appLinks.map((item, i) =>
                        <li key={i}><a href={item.href}>{item.name}</a></li>,
                    )}
                  </ul>
                </div>

                <div className="col-sm-3">
                  <h6>Find us here</h6>
                  <ul className="contact">
                    <li>
                      <i className="fa fa-envelope"/>
                      traduceri@mock-mail.com
                    </li>
                    <li>
                      <i className="fa fa-phone"/>
                      +40 111-111-111
                    </li>
                    <li>
                      <i className="fa fa-map-marker"/>
                      Cluj-Napoca
                    </li>
                  </ul>
                </div>

                <div className="col-sm-3">
                  <h6>Social</h6>
                  <ul className="social">
                    <li>
                      <a href="#"><i className="fa fa-facebook"/></a>
                    </li>
                    <li>
                      <a href="#"><i className="fa fa-linkedin"/></a>
                    </li>
                    <li>
                      <a href="#"><i className="fa fa-google-plus"/></a>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </footer>
    );
  }
}


